import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer, withText } from 'preact-i18n';

import mu from '../../lib/musher-util';

function getIcon(type) {
	switch (type) {
		case 'dogHistory':
			return 'fas fa-rss';
		case 'dogStatus':
			return 'fas fa-dog';
		case 'dogSpecialNeeds':
			return 'fas fa-dog';
		case 'dogImage':
			return 'fas fa-image';
		case 'dog':
			return 'fas fa-dog';
		case 'dogChanged':
			return 'fas fa-dog';
		case 'track':
			return 'fas fa-map-marked-alt';
		case 'trackChanged':
			return 'fas fa-map-marked-alt';
		case 'workoutPlan':
			return 'fas fa-calendar-alt';
		case 'workout':
			return 'fas fa-running';
		case 'newGarminWorkout':
			return 'fas fa-running';
		case 'newStravaWorkout':
			return 'fab fa-strava';
		case 'workoutChanged':
			return 'fas fa-running';
		case 'teamMember':
			return 'fas fa-user';
		case 'team':
			return 'fas fa-users';

        case 'newsLiked':
            return 'fas fa-thumbs-up';
        case 'newsCommentLiked':
        case 'newsCommentedLiked':
            return 'fas fa-thumbs-up';
        case 'newsCommentReplyLiked':
        case 'newsCommentedReplyLiked':
            return 'fas fa-thumbs-up'
        case 'newsComment':
        case 'newsCommented':
            return 'fas fa-comment';
        case 'newsCommentReply':
        case 'newsCommentedReply':
            return 'fas fa-comments';

        case 'trackLiked':
            return 'fas fa-thumbs-up';
        case 'trackCommentLiked':
        case 'trackCommentedLiked':
            return 'fas fa-thumbs-up';
        case 'trackCommentReplyLiked':
        case 'trackCommentedReplyLiked':
            return 'fas fa-thumbs-up'
        case 'trackComment':
        case 'trackCommented':
            return 'fas fa-comment';
        case 'trackCommentReply':
        case 'trackCommentedReply':
            return 'fas fa-comments';

        case 'workoutComment':
        case 'workoutCommented':
            return 'fas fa-comment';
        case 'workoutCommentReply':
        case 'workoutCommentedReply':
            return 'fas fa-comments'
        case 'workoutLiked':
            return 'fas fa-thumbs-up';
        case 'workoutCommentLiked':
        case 'workoutCommentedLiked':
            return 'fas fa-thumbs-up';
        case 'workoutCommentReplyLiked':
        case 'workoutCommentedReplyLiked':
            return 'fas fa-thumbs-up'; 

        case 'story':
            return 'fas fa-rss';
        case 'storyComment':
        case 'storyCommented':
            return 'fas fa-comment';
        case 'storyCommentReply':
        case 'storyCommentedReply':
            return 'fas fa-comments'
        case 'storyLiked':
            return 'fas fa-thumbs-up';
        case 'storyCommentLiked':
        case 'storyCommentedLiked':
            return 'fas fa-thumbs-up';
        case 'storyCommentReplyLiked':
        case 'storyCommentedReplyLiked':
            return 'fas fa-thumbs-up'; 

        case 'teamFollowRequest':
            return 'fas fa-project-diagram';
        case 'happyBirthday':
            return 'fas fa-birthday-cake';

        case 'trophy':
            return 'fas fa-trophy';
		default:
			return 'fas fa-rss';
	}
}

@observer
class AdminNotification extends Component {
    confirmRequest = () => {
        const { notification } = this.props;
        const { teamStore } = this.props.stores;
        teamStore.confirmFollowRequest({
            team: notification.team,
            user: notification.addedBy.id,
        });
    }

    deleteRequest = () => {
        const { notification } = this.props;
        const { teamStore } = this.props.stores;
        teamStore.deleteFollowRequest({
            team: notification.team,
            user: notification.addedBy.id,
        });
    }

    render() {
        const { notification, showDate } = this.props;
        const { addedBy: user  = {} } = notification;
        const displayNameShort = user.firstname ? `${util.ucfirst(user.firstname, true)}${util.ucfirst(user.lastname, true)}` : `${util.ucfirst(user.email || '', true)}`;
        const displayName = user.firstname ? `${user.firstname} ${user.lastname}` : `${user.email}`;
        const userImage = user.image || (user.images ? user.images[0] : undefined);

        const dogIds = [];
        const dogs = [];
        if (notification.grouped) {
            notification.grouped.forEach((e) => {
                if (e.dog && e.dog.id && dogIds.indexOf(e.dog.id) === -1) {
                    dogs.push(e.dog);
                    dogIds.push(e.dog.id);
                }
            });
        }
        const hasMultiElements = dogs.length > 0;
        const isDimmed = notification.read && notification.type !== 'teamFollowRequest';
        const isLinked =!hasMultiElements && notification.type !== 'teamFollowRequest';
        return (
            <div class='col-12 col-sm-8 col-lg-6 offset-sm-2 offset-lg-3 d-flex mb-2 position-relative' style={`opacity: ${isDimmed ? 0.6: 1};`}>
                <div 
                    class={`text-center border rounded-circle imageRoundedSmall imageRounded`} 
                    style={`padding-top: 7px; ${userImage ? `background-image: url("${userImage.s3ThumbLink}"); background-size: cover;` : ''}`}
                >
                    {!user.image && <span class='font-weight-lighter' style='font-size: 15px;'>{displayNameShort}</span>}
                </div>

                <div class='flex-grow-1 pl-3 pr-2'>
                    <span class='float-right ml-2'>
                        {notification.type && <span class='mr-2' style='font-size: 2.0em;'><i class={getIcon(notification.type)} /></span>}
                    </span>
                    <div style='font-size: 0.8em;'>
                        {isLinked ? <a href={notification.url}>
                            {notification.message}{notification.title ? `: ${notification.title}` : ''}
                        </a> : <>
                            {notification.message}
                        </>}
                        {notification.type === 'teamFollowRequest' && <>
                            <button class='btn btn-sm btn-danger float-right' onClick={this.deleteRequest}>
                                <Text id='notifications.delete'>Del</Text>
                            </button>
                            <button class='btn btn-sm btn-success float-right mr-2' onClick={this.confirmRequest}>
                                <Text id='notifications.confirm'>Confirm</Text>
                            </button>
                        </>}
                    </div>
                    <small class='text-muted'>
                        {notification.date && <span onClick={this.toggleDate} class='mr-2'>
                            {showDate ? util.formatDate(notification.date, { locale: 'no-NB' }) : util.formatDistance(notification.date, new Date(), { locale: 'no-NB' })}
                        </span>}
                        <i class='far fa-user' /> {displayName}
                        <br />
                        {dogs && dogs.map(d => <span class='mr-2'>
                            <a href={`/dogs/${d.id}`}><i class='fas fa-dog' /> {d.name}</a>
                        </span>)}
                        <br />
                    </small>
                </div>
            </div>
        );
    }
}

export default AdminNotification;